.navno-dekorator .siteheader .arbeidsflate {
  width: 100%;
  background-color: #f2f2f2;
  height: 2.75rem;
}
.navno-dekorator .siteheader .arbeidsflate__topp-liste-rad {
  width: 28rem;
  white-space: nowrap;
  margin: 0 auto;
}
.navno-dekorator .siteheader .arbeidsflate__liste-element {
  float: left;
  outline: 0;
}
.navno-dekorator .siteheader .arbeidsflate__lenke {
  display: inline-block;
  padding: 0.75rem 1rem 0;
  text-decoration: none;
  color: black;
}
.navno-dekorator .siteheader .arbeidsflate__lenke:focus,
.navno-dekorator .siteheader .arbeidsflate__lenke:active {
  outline: 3px solid #254b6d;
}
.navno-dekorator .siteheader .arbeidsflate__lenke-inner {
  padding-bottom: 0.6rem;
}
.navno-dekorator .siteheader .arbeidsflate__lenke-inner:hover {
  border-bottom: #005B82 2px solid;
}
.navno-dekorator .siteheader .arbeidsflate__lenke-inner--active {
  border-bottom: #005B82 2px solid;
}
.navno-dekorator .siteheader .arbeidsflate__lenke-inner--active:hover {
  border-bottom: #005B82 3px solid;
}
