.navno-dekorator .mobilmeny__content {
  padding: 0.5rem 0.5rem 0.5rem 1rem;
}
.navno-dekorator .mobilmeny__elementer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.navno-dekorator .mobilmeny__venstre-kolonne {
  padding-left: 0.5rem;
}
.navno-dekorator .mobilmeny__hoyre-kolonne {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  padding-top: 0.5rem;
  flex-wrap: wrap;
}
.navno-dekorator .mobilmeny__logo {
  width: 66px;
  height: 66px;
}
.navno-dekorator .mobilmeny__logo:hover {
  outline: 1px solid black;
}
.navno-dekorator .mobilmeny__logo:focus {
  outline: 3px solid #254b6d;
}
.navno-dekorator .mobilmeny__sok {
  margin-left: 3px;
  text-decoration: none;
  color: black;
  background-color: white;
  border: none;
  padding: 0.4rem 0.7rem 0.4rem 0.7rem;
  order: 2;
}
.navno-dekorator .mobilmeny__sok:hover {
  outline: 1px black solid;
}
.navno-dekorator .mobilmeny__sok:focus {
  outline: 3px solid #254b6d;
}
.navno-dekorator .mobilmeny .login-container {
  order: 3;
}
.navno-dekorator .mobilmeny .dropdown-container {
  order: 4;
  width: 100%;
}
@media (max-width: 330px) {
  .navno-dekorator .mobilmeny__sok {
    padding: 0.4rem 0.2rem;
  }
}
