.navno-dekorator .hovedmeny-desktop__innhold-wrapper {
  position: absolute;
  z-index: 1001;
  overflow: hidden;
  max-height: 0;
  max-width: 341.33333333px;
  visibility: hidden;
  background-color: white;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  transition-duration: 0.7s;
  transition-timing-function: ease-in-out;
  transition-property: all;
}
.navno-dekorator .hovedmeny-desktop__innhold-wrapper--aktive {
  max-height: 60rem;
  max-width: 100%;
  visibility: visible;
}
.navno-dekorator .hovedmeny-desktop__innhold {
  box-sizing: border-box;
  padding: 0 3rem 2rem 3rem;
  overflow: hidden;
  min-width: 768px;
}
@media (min-width: 1024px) {
  .navno-dekorator .hovedmeny-desktop__innhold {
    width: 1024px;
  }
}
