.delskjerm__modal {
  max-width: 35rem;
  overflow: visible !important;
}
@media (min-width: 768px) {
  .navno-dekorator .delskjerm__content {
    padding: 3rem 4rem 2rem 4rem;
  }
}
@media (max-width: 768px) {
  .navno-dekorator .delskjerm__content {
    padding-top: 2rem;
  }
}
.navno-dekorator .delskjerm__beskrivelse {
  margin: 1rem 0 1.5rem 0;
}
.navno-dekorator .delskjerm__header {
  width: 100%;
  top: -2rem;
  left: 0rem;
  display: flex;
  position: absolute;
  justify-content: center;
}
.navno-dekorator .delskjerm__veileder {
  padding-top: 0.25rem;
  height: 4.5rem;
  width: 4.5rem;
  border-radius: 2.25rem;
  background-color: #254b6d;
}
.navno-dekorator .delskjerm__knapper {
  margin-top: 1rem;
  display: flex;
}
@media (max-width: 768px) {
  .navno-dekorator .delskjerm__knapper {
    flex-direction: column;
  }
}
.navno-dekorator .delskjerm__knapper > * {
  margin: 0.5rem 0.5rem 0 0;
}
