.navno-dekorator .lenke {
  color: #0067C5;
  background: none;
  text-decoration: underline;
  cursor: pointer;
}
.navno-dekorator .lenke:hover,
.navno-dekorator .lenke--hover {
  text-decoration: none;
}
.navno-dekorator .lenke:focus,
.navno-dekorator .lenke--focus {
  outline: none;
  color: white;
  text-decoration: none;
  background-color: #254b6d;
  box-shadow: 0 0 0 2px #254b6d;
}
.navno-dekorator .lenke:active,
.navno-dekorator .lenke--active {
  color: white;
}
