.navno-dekorator .mobilmeny__innhold-wrapper,
.navno-dekorator .mobilmeny__varsel-innhold {
  position: fixed;
  top: 0;
  left: -80%;
  z-index: 1001;
}
.navno-dekorator .mobilmeny ul {
  padding: 0;
}
.navno-dekorator .mobilmeny__undermeny-innhold,
.navno-dekorator .mobilmeny__varsel-innhold {
  top: 0;
  left: -80%;
  width: 80%;
  position: fixed;
}
.navno-dekorator .mobilmeny__undermeny-innhold--active,
.navno-dekorator .mobilmeny__varsel-innhold--active {
  left: 0;
}
.navno-dekorator .mobilmeny__innhold-wrapper,
.navno-dekorator .mobilmeny__undermeny-innhold,
.navno-dekorator .mobilmeny__varsel-innhold {
  padding: 1rem;
  background-color: white;
  width: 80%;
  max-width: 375px;
  height: 100%;
  border-radius: 3px;
  overflow: scroll;
  transition: all 0.7s ease-in-out;
}
.navno-dekorator .mobilmeny__innhold-wrapper--aktive,
.navno-dekorator .mobilmeny__undermeny-innhold--aktive,
.navno-dekorator .mobilmeny__varsel-innhold--aktive {
  left: 0;
}
.navno-dekorator .mobilmeny__meny--list ul {
  margin: 0;
}
.navno-dekorator .mobilmeny__meny--list {
  padding: 1rem 1rem 0.5rem 1rem;
}
.navno-dekorator .mobilmeny__meny--list .lenke {
  text-decoration: none;
}
.navno-dekorator .mobilmeny__meny--list .lenke:hover {
  text-decoration: underline;
}
.navno-dekorator .mobilmeny__meny--list .lenke:active,
.navno-dekorator .mobilmeny__meny--list .lenke:focus,
.navno-dekorator .mobilmeny__meny--list .lenke:focus-within {
  box-shadow: none;
}
.navno-dekorator .mobilmeny__meny--list .lenke:active span,
.navno-dekorator .mobilmeny__meny--list .lenke:focus span,
.navno-dekorator .mobilmeny__meny--list .lenke:focus-within span {
  color: #005B82;
  outline: 3px solid #254b6d;
}
.navno-dekorator .mobilmeny__meny--listItem {
  padding-bottom: 2rem;
  padding-right: 0.5rem;
}
.navno-dekorator .mobilmeny__meny--tilbakelenke {
  padding-bottom: 0;
}
.navno-dekorator .mobilmeny__meny--text-element {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.navno-dekorator .mobilmeny__meny--text-element-undermeny {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}
.navno-dekorator .mobilmeny__meny--ingress {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem 1.5rem 0 0;
  color: #59514B;
}
.navno-dekorator .mobilmeny__meny--ingress .typo-sidetittel {
  font-weight: 500;
}
.navno-dekorator .mobilmeny__undermeny-chevron {
  padding-right: 0.5rem;
}
.navno-dekorator .mobilmeny__lukk-undermeny .lenke {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  padding: 1rem 1rem 1rem 0;
}
.navno-dekorator .mobilmeny__lukk-undermeny .lenke i {
  margin-right: 0.5rem;
}
.navno-dekorator .mobilmeny__lukk-undermeny .lenke:active {
  color: #005B82;
}
.navno-dekorator .mobilmeny .min-varsel-wrapper .mobilmeny__lukk-undermeny .lenke {
  padding-top: 0;
  padding-bottom: 2rem;
}
.navno-dekorator .mobilmeny__minside-rad {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 1rem 0 0.5rem;
}
.navno-dekorator .mobilmeny__minside-rad--sett-rad-midt {
  justify-content: flex-start;
}
