.navno-dekorator .chevron--ned {
  display: inline-block;
  flex-shrink: 0;
  height: 2px;
  line-height: 2px;
  width: 20px;
  position: relative;
  text-align: center;
  transition: 200ms transform;
  vertical-align: middle;
}
.navno-dekorator .chevron--ned:before,
.navno-dekorator .chevron--ned:after {
  transition: 200ms transform;
  border-radius: 10px;
}
.navno-dekorator .chevron--ned:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 50%;
  background: currentColor;
}
.navno-dekorator .chevron--ned:after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: 50%;
  background: currentColor;
}
.navno-dekorator .chevron--ned:before {
  transform: translateX(2px) rotate(45deg);
}
.navno-dekorator .chevron--ned:after {
  transform: translateX(-2px) rotate(-45deg);
}
.navno-dekorator .chevron--ned.chevron--stor {
