.sok-modal {
  width: 100%;
  height: 100%;
}
.sok-modal__overlay {
  min-width: 100%;
  max-width: 450px;
  height: 100%;
  max-height: 800px;
  background-color: #254b6d;
  border-radius: 0;
}
.sok-modal__overlay ul {
  padding: 0;
}
.sok-modal__overlay section {
  width: 100%;
  height: 86%;
}
.sok-modal__overlay button.lukknapp {
  display: none;
}
.sok-modal__body {
  padding: 1rem;
}
.sok-modal__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.sok-modal__veiledende-tekst {
  margin: 3rem 0 1rem 0;
  color: white;
}
.sok-modal__logo .nav-brand-lenke:focus {
  outline: 2px solid white;
}
.sok-modal__logo .nav-brand-lenke .modal-logo-svg {
  outline: 0;
}
