.navno-dekorator .hovedmeny-desktop__dropdown {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.navno-dekorator .hovedmeny-desktop__topp-seksjon {
  margin: 1.5rem -3rem;
  padding: 1rem 3rem;
}
.navno-dekorator .hovedmeny-desktop__topp-seksjon-lenke {
  text-decoration: underline;
}
.navno-dekorator .hovedmeny-desktop__topp-seksjon-lenke:hover {
  text-decoration: none;
}
.navno-dekorator .hovedmeny-desktop__topp-seksjon-tittel {
  margin-top: 0.15em;
}
.navno-dekorator .hovedmeny-desktop__hoved-seksjon {
  display: flex;
  flex-grow: 6;
  flex-wrap: wrap;
  justify-content: flex-start;
}
@media (max-width: 896px) {
  .navno-dekorator .hovedmeny-desktop__hoved-seksjon {
    --num-cols: 3;
  }
  .navno-dekorator .hovedmeny-desktop__hoved-seksjon > * {
    flex-grow: 1;
    flex-shrink: 0;
    flex-basis: calc(25% + 1px);
    max-width: calc(33.33333333% - (1.5em - 1.5em / 3));
  }
  .navno-dekorator .hovedmeny-desktop__hoved-seksjon > *:not(:nth-child(3n)) {
    margin-right: 1.5em;
  }
}
@media (min-width: 896px) {
  .navno-dekorator .hovedmeny-desktop__hoved-seksjon {
    --num-cols: 4;
  }
  .navno-dekorator .hovedmeny-desktop__hoved-seksjon > * {
    flex-grow: 1;
    flex-shrink: 0;
    flex-basis: calc(20% + 1px);
    max-width: calc(25% - (1.5em - 1.5em / 4));
  }
  .navno-dekorator .hovedmeny-desktop__hoved-seksjon > *:not(:nth-child(4n)) {
    margin-right: 1.5em;
  }
}
@media (min-width: 1024px) {
  .navno-dekorator .hovedmeny-desktop__hoved-seksjon {
    --num-cols: 4;
  }
  .navno-dekorator .hovedmeny-desktop__hoved-seksjon > * {
    flex-grow: 1;
    flex-shrink: 0;
    flex-basis: calc(20% + 1px);
    max-width: calc(25% - (1.5em - 1.5em / 4));
  }
  .navno-dekorator .hovedmeny-desktop__hoved-seksjon > *:not(:nth-child(4n)) {
    margin-right: 1.5em;
  }
}
.navno-dekorator .hovedmeny-desktop__hoved-seksjon-tema {
  margin-bottom: 2rem;
}
.navno-dekorator .hovedmeny-desktop__hoved-seksjon-tema-tittel {
  margin-bottom: 0.5em;
}
.navno-dekorator .hovedmeny-desktop__hoved-seksjon-tema-lenker {
  margin: 0;
  padding: 0;
  list-style: none;
}
.navno-dekorator .hovedmeny-desktop__hoved-seksjon-tema-lenker li {
  margin-bottom: 0.5rem;
}
.navno-dekorator .hovedmeny-desktop__hoved-seksjon-tema-lenker li .lenke {
  text-decoration: underline;
}
.navno-dekorator .hovedmeny-desktop__hoved-seksjon-tema-lenker li .lenke:hover {
  text-decoration: none;
}
.navno-dekorator .hovedmeny-desktop__bunn-separator {
  width: 100%;
  padding: 0;
  margin: 0;
  border: none;
  border-top: 1px solid #78706A;
}
.navno-dekorator .hovedmeny-desktop__bunn-seksjon {
  display: flex;
  justify-content: flex-start;
  margin-top: 2rem;
  --num-cols: 3;
}
.navno-dekorator .hovedmeny-desktop__bunn-seksjon > * {
  flex-grow: 1;
  flex-shrink: 0;
  flex-basis: calc(25% + 1px);
  max-width: calc(33.33333333% - (2.5em - 2.5em / 3));
}
.navno-dekorator .hovedmeny-desktop__bunn-seksjon > *:not(:nth-child(3n)) {
  margin-right: 2.5em;
}
.navno-dekorator .hovedmeny-desktop__bunn-lenke {
  text-decoration: underline;
}
.navno-dekorator .hovedmeny-desktop__bunn-lenke:hover {
  text-decoration: none;
}
.navno-dekorator .hovedmeny-desktop__bunn-lenke-stikkord {
  display: inline-block;
  list-style-type: disc;
  list-style-position: inside;
  margin: 0;
  padding: 0;
}
.navno-dekorator .hovedmeny-desktop__bunn-lenke-stikkord li {
  display: inline-block;
}
.navno-dekorator .hovedmeny-desktop__bunn-lenke-stikkord li:not(:first-of-type) .bullet::before {
  content: '';
  margin: 0 0.25em;
}
