.navno-dekorator .desktopmeny__bakgrunn {
  z-index: 999;
  visibility: hidden;
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  transition-duration: 0.35s;
  transition-timing-function: ease-in;
  transition-property: all;
}
.navno-dekorator .desktopmeny__bakgrunn--active {
  visibility: visible;
  background-color: rgba(0, 0, 0, 0.35);
  -webkit-backdrop-filter: blur(2px);
          backdrop-filter: blur(2px);
}
.navno-dekorator .mobilmeny__bakgrunn {
  display: none;
  position: fixed;
  z-index: 999;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.4);
}
.navno-dekorator .mobilmeny__bakgrunn--active {
  display: initial;
}
