.navno-dekorator .site-skiplinks ul {
  margin: 0;
}
.navno-dekorator .site-skiplinks li > .visuallyhidden.focusable:focus,
.navno-dekorator .site-skiplinks li > .visuallyhidden.focusable:active {
  height: 2.625rem;
}
.navno-dekorator .site-skiplinks .visuallyhidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
}
.navno-dekorator .site-skiplinks .visuallyhidden.focusable:focus,
.navno-dekorator .site-skiplinks .visuallyhidden.focusable:active {
  clip: auto;
  height: auto;
  width: auto;
  padding: 0.8rem;
  margin: 0;
  overflow: visible;
}
.navno-dekorator .site-skiplinks .visuallyhidden.focusable:focus {
  outline: none;
  color: white;
  text-decoration: none;
  background-color: #254b6d;
  box-shadow: 0 0 0 2px #254b6d;
}
.navno-dekorator .dekorator-under-arbeid {
  padding: 0.5rem 0;
  width: 100%;
  height: auto;
  text-align: center;
  background-color: #3E3832;
  color: white;
}
@media (min-width: 768px) {
  .navno-dekorator .dekorator-under-arbeid {
    height: 2.75rem;
  }
}
