.sitefooter {
  padding: 1rem;
  width: 100%;
  min-height: 12.5rem;
  border-bottom: #3E3832 2px solid;
}
.navno-dekorator .sitefooter .nav-frontend-chevron {
  color: #0067C5;
}
.navno-dekorator .sitefooter .lenke {
  text-decoration: none;
}
.navno-dekorator .sitefooter .lenke:hover {
  text-decoration: underline;
}
.navno-dekorator .sitefooter__innhold {
  text-align: left;
  padding: 0 1rem;
}
.navno-dekorator .sitefooter__menylinje-topp .menylenker-seksjon .typo-undertittel {
  font-weight: bolder;
}
.navno-dekorator .sitefooter__menylinje-topp ul,
.navno-dekorator .sitefooter__menylinje-arbeidsflatevalg ul {
  margin-top: 0.8rem;
}
.navno-dekorator .sitefooter__menylinje-topp li,
.navno-dekorator .sitefooter__menylinje-arbeidsflatevalg li {
  margin-bottom: 1.3rem;
}
@media (min-width: 768px) {
  .sitefooter {
    padding: 1.5rem 2rem 5rem;
  }
  .navno-dekorator .sitefooter__innhold {
    width: auto;
    margin: 0 auto;
    padding: 0;
    text-align: initial;
    position: relative;
  }
  .navno-dekorator .sitefooter__menylinje-topp {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 1.5rem;
  }
  .navno-dekorator .sitefooter__menylinje-topp .menylenker-seksjon {
    width: 33.3%;
  }
  .navno-dekorator .sitefooter__menylinje-arbeidsflatevalg {
    display: flex;
    align-items: center;
    margin-bottom: 1.5rem;
  }
  .navno-dekorator .sitefooter__menylinje-arbeidsflatevalg .ga-til-innhold-for {
    margin-right: 1rem;
  }
  .navno-dekorator .sitefooter__menylinje-arbeidsflatevalg .arbeidsflatevalg {
    width: 30rem;
    display: flex;
    align-items: center;
  }
  .navno-dekorator .sitefooter__menylinje-arbeidsflatevalg .arbeidsflatevalg ul {
    margin-top: 0;
  }
  .navno-dekorator .sitefooter__menylinje-arbeidsflatevalg .arbeidsflatevalg li {
    min-width: 9rem;
    max-width: 14rem;
    margin-bottom: 0;
  }
  .navno-dekorator .sitefooter__menylinje-bottom .menylinje-bottom__innhold {
    display: flex;
    align-items: center;
  }
  .navno-dekorator .sitefooter__menylinje-bottom .menylinje-bottom__innhold ul {
    width: 24.5rem;
    margin: 0;
  }
  .navno-dekorator .sitefooter__menylinje-bottom .menylinje-bottom__innhold .menylinje-bottom__logo {
    width: 65px;
    height: 65px;
    margin-right: 1rem;
  }
  .navno-dekorator .sitefooter__menylinje-bottom .menylinje-bottom__innhold .menylinje-bottom__tekst {
    width: 11rem;
    margin-right: 1rem;
  }
}
@media (min-width: 1024px) {
  .navno-dekorator .sitefooter__innhold {
    width: 1024px;
    margin: 0 auto;
  }
  .navno-dekorator .sitefooter__menylinje-topp .menylenker-seksjon {
    width: 22%;
  }
  .navno-dekorator .sitefooter__menylinje-bottom .menylinje-bottom__innhold .menylinje-bottom__logo {
    position: absolute;
    left: -90px;
    bottom: -23px;
    margin-right: 0;
  }
}
