.navno-dekorator .dropdown__menyknapp {
  text-decoration: none;
  color: black;
  background-color: white;
  border: none;
  padding: 0;
}
.navno-dekorator .dropdown__menyknapp:focus {
  text-decoration: none;
  box-shadow: none;
  outline: #005B82 3px solid;
  border-radius: 4px;
}
.navno-dekorator .dropdown__menyknapp--innhold {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0.4rem 0.7rem;
  width: -moz-fit-content;
  width: fit-content;
}
.navno-dekorator .dropdown__menyknapp--innhold:hover {
  outline: 1px black solid;
  cursor: pointer;
}
.navno-dekorator .dropdown__menyknapp--innhold .hamburger-ikon {
  margin-right: 0.5rem;
}
@media (max-width: 330px) {
  .navno-dekorator .dropdown__menyknapp--innhold {
    padding: 0.4rem 0.2rem;
  }
}
@media (min-width: 768px) {
  .navno-dekorator .dropdown__menyknapp {
    border-radius: 3px;
  }
  .navno-dekorator .dropdown__menyknapp--innhold {
    padding: 0.4rem 0.7rem 0;
  }
  .navno-dekorator .dropdown__menyknapp--innhold .hamburger-ikon {
    margin-right: 0;
  }
}
