.navno-dekorator .alertstripe {
  background-color: #FFF;
  display: block;
  padding: 1rem 1rem 1rem 1rem;
  border-radius: 4px;
  display: flex;
  align-content: center;
  border-radius: 0.25rem;
  color: #3E3832;
}
.navno-dekorator .alertstripe--info {
  border: 1px solid #66CBEC;
  background-color: #e0f5fb;
}
.navno-dekorator .alertstripe--suksess {
  border: 1px solid #06893A;
  background-color: #9bd0b0;
}
.navno-dekorator .alertstripe--advarsel {
  border: 1px solid #FF9100;
  background-color: #ffe9cc;
}
.navno-dekorator .alertstripe--feil {
  border: 1px solid #BA3A26;
  background-color: #e79999;
}
.navno-dekorator .alertstripe__ikon {
  margin-right: 0.75rem;
  display: flex;
  top: 1rem;
  flex-shrink: 0;
}
.navno-dekorator .alertstripe .lenke:not(:focus) {
  color: #3E3832;
}
.navno-dekorator .alertstripe .alertstripe__tekst {
  margin: auto 0;
  max-width: 37.5rem;
  display: block;
  flex: 1;
}
