.navno-dekorator .mobil-arbeidsflate-valg {
  padding: 0;
  margin: 0;
}
.navno-dekorator .mobil-arbeidsflate-valg .lenke {
  text-decoration: none;
  display: flex;
  align-items: center;
  margin-bottom: 1rem;
  width: 90%;
}
.navno-dekorator .mobil-arbeidsflate-valg .lenke:hover {
  text-decoration: underline;
}
.navno-dekorator .mobil-arbeidsflate-valg .lenke:focus {
  color: #005B82;
  box-shadow: none;
  background-color: white;
  outline: 3px solid #254b6d;
}
.navno-dekorator .mobil-arbeidsflate-valg__lenke-tittel {
  text-transform: lowercase;
}
