.navno-dekorator .desktopmeny .varselbjelle,
.navno-dekorator .mobilmeny .varselbjelle {
  position: relative;
  width: 31px;
}
.navno-dekorator .desktopmeny .varselbjelle .toggle-varsler-container,
.navno-dekorator .mobilmeny .varselbjelle .toggle-varsler-container {
  display: inline-block;
  height: 1.9375rem;
  width: 1.9375rem;
  border: 1px solid transparent;
  vertical-align: middle;
}
@media (min-width: 48.0625rem) {
  .navno-dekorator .desktopmeny .varselbjelle .toggle-varsler-container,
  .navno-dekorator .mobilmeny .varselbjelle .toggle-varsler-container {
    display: inline-block;
  }
}
.navno-dekorator .desktopmeny .varselbjelle .toggle-varsler-container .toggle-varsler,
.navno-dekorator .mobilmeny .varselbjelle .toggle-varsler-container .toggle-varsler {
  background: url(/dekoratoren/./media/alarm.svg) no-repeat center;
  background-size: 1.5rem 1.5rem;
  width: 100%;
  height: 100%;
  border: 2px solid transparent;
}
.navno-dekorator .desktopmeny .varselbjelle .toggle-varsler-container .toggle-varsler:hover,
.navno-dekorator .mobilmeny .varselbjelle .toggle-varsler-container .toggle-varsler:hover {
  background-image: url(/dekoratoren/./media/alarm_hover.svg);
}
.navno-dekorator .desktopmeny .varselbjelle .toggle-varsler-container .toggle-varsler:active,
.navno-dekorator .mobilmeny .varselbjelle .toggle-varsler-container .toggle-varsler:active {
  background-image: url(/dekoratoren/./media/alarm_active.svg);
}
.navno-dekorator .desktopmeny .varselbjelle .toggle-varsler-container .toggle-varsler:focus,
.navno-dekorator .mobilmeny .varselbjelle .toggle-varsler-container .toggle-varsler:focus {
  outline: 0;
  border: 2px solid #254b6d;
  border-radius: 4px;
}
.navno-dekorator .desktopmeny .varselbjelle .toggle-varsler-container.har-nye-varsler .toggle-varsler,
.navno-dekorator .mobilmeny .varselbjelle .toggle-varsler-container.har-nye-varsler .toggle-varsler {
  background-image: url(/dekoratoren/./media/alarm-nytt-varsel.svg);
}
.navno-dekorator .desktopmeny .varselbjelle .toggle-varsler-container.har-nye-varsler .toggle-varsler:hover,
.navno-dekorator .mobilmeny .varselbjelle .toggle-varsler-container.har-nye-varsler .toggle-varsler:hover {
  background-image: url(/dekoratoren/./media/alarm-nytt-varsel_hover.svg);
}
.navno-dekorator .desktopmeny .varselbjelle .toggle-varsler-container.har-nye-varsler .toggle-varsler:active,
.navno-dekorator .mobilmeny .varselbjelle .toggle-varsler-container.har-nye-varsler .toggle-varsler:active {
  background-image: url(/dekoratoren/./media/alarm-nytt-varsel_active.svg);
}
