.navno-dekorator .desktopmeny {
  width: 100%;
  padding: 0.5rem 0 1rem;
  border-bottom: 2px solid #0067C5;
}
.navno-dekorator .desktopmeny__content {
  width: 100%;
  margin: 0 auto;
  padding: 0;
}
.navno-dekorator .desktopmeny__elementer {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0;
}
.navno-dekorator .desktopmeny__nav-brand {
  order: 1;
  width: auto;
}
.navno-dekorator .desktopmeny__nav-brand:focus {
  outline: 3px solid #254b6d;
}
.navno-dekorator .desktopmeny__nav-brand svg:focus {
  outline: 0;
}
.navno-dekorator .desktopmeny .hovedmeny-desktop__menyknapp {
  order: 2;
  width: auto;
}
.navno-dekorator .desktopmeny .sok {
  order: 3;
  width: auto;
  max-height: 40px;
}
.navno-dekorator .desktopmeny .minsidelenke-varselbjelle {
  display: none;
}
.navno-dekorator .desktopmeny .login-container {
  order: 3;
  width: auto;
  max-height: 40px;
}
.navno-dekorator .desktopmeny .dropdown-container {
  order: 4;
  width: 100%;
}
@media (min-width: 1024px) {
  .navno-dekorator .desktopmeny__content {
    width: 1024px;
  }
  .navno-dekorator .desktopmeny .minsidelenke-varselbjelle {
    min-width: 9.375rem;
    order: 3;
  }
  .navno-dekorator .desktopmeny .minsidelenke-varselbjelle .minside-lenke {
    width: 6.375rem;
    max-height: 40px;
    display: inline-block;
  }
  .navno-dekorator .desktopmeny .minsidelenke-varselbjelle .varselbjelle {
    margin-left: 1rem;
    width: auto;
    max-height: 40px;
    display: inline-block;
  }
}
