.navno-dekorator .login-container:hover {
  outline: 1px solid black;
}
.navno-dekorator .login-container .mobil-login-knapp {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0.4rem 0.7rem 0.4rem 0.7rem;
  text-decoration: none;
  color: black;
  background-color: white;
  border: none;
  margin-left: 3px;
}
.navno-dekorator .login-container .mobil-login-knapp:focus {
  outline: 3px solid #254b6d;
}
@media (max-width: 330px) {
  .navno-dekorator .login-container .mobil-login-knapp {
    padding: 0.4rem 0.2rem;
  }
}
@media (min-width: 1024px) {
  .navno-dekorator .login-container {
    margin-left: 3px;
  }
  .navno-dekorator .login-container:hover {
    outline: 0;
  }
}
