.navno-dekorator .mobilmeny__meny--top {
  width: 100%;
  max-height: 6rem;
  padding: 1rem 0 2rem 0;
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
  position: relative;
}
.mobilmeny__meny--top a.nav-brand-lenke svg.modal-logo-svg {
  width: 73px;
  height: 46px;
}
.mobilmeny__meny--top a.nav-brand-lenke svg.modal-logo-svg:focus {
  outline: none;
}
.navno-dekorator .mobilmeny__meny--top a.nav-brand-lenke:focus {
  outline: 2px solid #254b6d;
}
.navno-dekorator .mobilmeny__meny--lukkmeny-ramme {
  height: 44px;
}
.navno-dekorator .mobilmeny__lukkmeny-knapp {
  width: 44px;
  height: 44px;
  border: none;
  border-radius: 50%;
  position: relative;
  cursor: pointer;
  display: inline-block;
  margin: 0 1rem;
  background-color: #cce1f3;
}
.navno-dekorator .mobilmeny__lukkmeny-knapp:after {
  left: 50%;
  top: 50%;
  margin-left: -11px;
  width: 22px;
  height: 2px;
  background-color: #0067C5;
  content: '';
  position: absolute;
  transform: rotate(-45deg);
}
.navno-dekorator .mobilmeny__lukkmeny-knapp:before {
  left: 50%;
  top: 50%;
  margin-left: -11px;
  width: 22px;
  height: 2px;
  background-color: #0067C5;
  content: '';
  position: absolute;
  transform: rotate(45deg);
}
.navno-dekorator .mobilmeny__lukkmeny-knapp:focus {
  outline: 2px solid #254b6d;
}
.navno-dekorator .mobilmeny__lukkmeny-knapp:hover {
  transform: scale(1.1);
}
.navno-dekorator .mobilmeny__lukkmeny-knapp[data-animation='beat-rotation']:not(.heartbeat) {
  transition: 200ms cubic-bezier(0.175, 0.885, 0.52, 1.775);
}
@keyframes beat {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0.8);
  }
}
.navno-dekorator .mobilmeny .heartbeat {
  animation: beat 0.1s 2 ease-in-out alternate;
}
